figure.snip1383 {
  font-family: 'Raleway', Arial, sans-serif;
  position: relative;
  overflow: hidden;
  margin: 10px;
  min-width: 230px;
  max-width: 315px;
  /* max-height: 220px; */
  height: 220px;
  width: 100%;
  color: #ffffff;
  text-align: left;
  background-color: whitesmoke;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}
figure.snip1383 * {
  box-sizing: border-box;
  transition: all 0.35s ease;
  color: #ffffff;
}
figure.snip1383 img {
  max-width: 100%;
  transition-delay: 0.2s;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
figure.snip1383 figcaption {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  transform: scale(0.5) translate(0%, -50%);
  transform-origin: 50% 0%;
  z-index: 1;
  opacity: 0;
  padding: 0 30px;
  background-color: #07090c;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
figure.snip1383 span,
figure.snip1383 p {
  line-height: 1.5em;
}
figure.snip1383 span {
  margin: 0;
  font-weight: 800;
  text-transform: uppercase;
}
figure.snip1383 p {
  font-size: 0.8em;
  font-weight: 500;
  margin: 0 0 15px;
}

figure.snip1383:hover img,
figure.snip1383.hover img {
  -webkit-animation: snip1383 0.45s linear forwards;
  animation: snip1383 0.45s linear forwards;
  -webkit-animation-iteration-count: 1;
  animation-iteration-count: 1;
}
figure.snip1383:hover figcaption,
figure.snip1383.hover figcaption {
  transform: scale(1) translate(0, -50%);
  opacity: 1;
  transition-delay: 0.35s;
}
@-webkit-keyframes snip1383 {
  50% {
    transform: scale(0.8) translateY(0%);
    opacity: 0.5;
  }
  100% {
    transform: scale(0.8) translateY(150%);
    opacity: 0.5;
  }
}
@keyframes snip1383 {
  50% {
    transform: scale(0.8) translateY(0%);
    opacity: 0.5;
  }
  100% {
    transform: scale(0.8) translateY(150%);
    opacity: 0.5;
  }
}
